// 引入vue
import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './app';
import vheader from 'component/mui/Header.vue';

Vue.use(VueRouter);
var router = new VueRouter();
//head所用参数
var hdstyle = {
        back: {
            display: true
        },
        title: {
            display: true
        },
        rightButton: {
            display: true,
            text: '首页',
            onClick: function() {
                muiUtils.openIndexWindow('buyer');
            }
        }
    };
//头部head start
var HeaderVue = Vue.extend(vheader);
var header = new HeaderVue({
    propsData: {
        hdstyle: hdstyle,
        title: '商品详情',
        showHeader: true
    },
    el: 'vheader'
});
//头部head end

//mui生命周期管理
mui.init({
    swipeBack: false,
});

mui.plusReady(function() {
//  header.showHeader = plus.webview.currentWebview().isshowxiajia;
    router.start(app, '#app');
});